import { g as getLoggedInUser } from "../../../../../chunks/user.js";
import { r as redirect, e as error } from "../../../../../chunks/index.js";
import { a as nodeMap, g as gameIdByNaturalId, d as db } from "../../../../../chunks/node-map.js";
